nav {
    padding: 1rem 1em; }

.navbar {
    li {
        &:hover {
            background-color: #ddd;
            a {
                color: #fff; } }
        a {
	        color: #888; }
        &:not(.active) {
            a {
                padding-top: calc(0.5rem + 4px); } }
        &.active {
        	color: #888;
        	border-top: 4px solid wheat;
        	background-color: #f8f8f8; } } }

.navbar-nav {
    font-size: 14px;
    color: #888;
    text-transform: uppercase;
    font-weight: bold; }

.navbar-right {
    span {
        color: #888; } }

.nav-bot {
    height: 138px;
    background-color: #f8f8f8;
    box-shadow: 0 5px 15px 0 rgba(0,0,0,.05) inset, 0 -5px 15px 0 rgba(0,0,0,.05) inset;
    padding: 2rem 0;
    @media(max-width: 767px) {
        display: none !important;
        padding: 0; }
    &.sticky {
        position: fixed;
        top: 0;
        z-index: 999;
        width: 100%;
        + main {
            padding-top: 138px;
            @media(max-width: 767px) {
                padding-top: 344.6px; } } }
    .grid {
        padding: 15px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 10px;
        grid-row-gap: 10px;
        @media(max-width: 767px) {
            grid-template-columns: 1fr;
            grid-row-gap: 0px;
            grid-template-rows: repeat(6, 1fr); }
        .input1 {
            grid-area: 1 / 1 / 2 / 2;
            @media(max-width: 767px) {
                grid-area: 1 / 1 / 2 / 2; } }
        .input2 {
            grid-area: 1 / 2 / 2 / 3;
            @media(max-width: 767px) {
                grid-area: 2 / 1 / 3 / 2; } }
        .input3 {
            grid-area: 1 / 3 / 2/ 4;
            @media(max-width: 767px) {
                grid-area: 3 / 1 / 4 / 2; } }
        .input4 {
            grid-area: 2 / 1 / 3 / 3;
            @media(max-width: 767px) {
                grid-area: 4 / 1 / 5 / 2; } }
        .input5 {
            grid-area: 2 / 3 / 3/ 5;
            @media(max-width: 767px) {
                grid-area: 5 / 1 / 6 / 2; } }
        .grid-button {
            grid-area: 1 / 4 / 2 / 5;
            @media(max-width: 767px) {
                grid-area: 6 / 1 / 7 / 2; } } }
    form {
        width: 100%;
        .input-group {
            margin: 0.25rem 0;
            .input-group-text {
            	background-color: wheat;
            	border: none; } }
        .hideForm {
            display: none; } } }
