body, html {
	font-family: Tajawal;
	font-size: 19px; }
img {
    max-width: 100%; }
.form-group {
    &.required {
        label:after {
            content: " *";
            color: red; } } }
button {
	background-color: $primary;
	color: #555;
	border: none;
	font-weight: bold; }

h1,h2,h3 {
    color:  $secondary;
    text-transform: uppercase; }

.title {
    width: 100%;
    text-align: center;
    font-size: 3rem; }

h2 {
    text-align: center;
    font-size: 2rem; }

.text-bold {
	font-weight: bold; }
